/* hero
----------------------------------------- */
@use "../../global" as *;

/*==============================================================
p-hero
==============================================================*/

/* hero
----------------------------------------- */
.p-hero {
    position: relative;
    margin-top: 6rem;
}
.p-hero figure {
    height: 28rem;
}
.p-hero figure picture img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media screen and (min-width: 1440px) {
    .p-hero {
        margin-top: rem(118);
    }
}

@media screen and (min-width: 1024px) {
    .p-hero figure {
        height: initial;
    }
}
