/* header
----------------------------------------- */
@use "../global" as *;

body {
  &.subpage {
    .l-header {
      background: #fff;
      opacity: 1;
      transform: translateY(0);

      @include responsive(xxl) {
        height: rem(118);
      }
    }

    .l-header__logoLink {
      display: none;
    }

    .l-header__logoLink--subpage {
      display: block;

      & figure {
        picture {
          img {
            width: 100%;
          }
        }
      }
    }

    .l-header__button {
      @include responsive(lg2) {
        top: rem(38);
        right: rem(122);
      }
    }
  }
}

body.is-active {
  height: 100%;
  overflow: hidden;
}

.l-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 7;
  width: 100%;
  height: 60px;
  padding: rem(10);
  @include flex(space-between, center, row);
  opacity: 1;

  @include responsive(lg2) {
    padding: rem(26) rem(30);
  }
}

.l-header__inner {
  width: 100%;

  @include flex(space-between, center, row);
}

.l-header__logo {
  position: relative;
  z-index: 3;
  width: rem(151);
  max-width: 251px;
  // padding-top: rem(18);

  @include responsive(lg2) {
    width: rem(251);
    // max-width: 251px;
    position: absolute;
    top: rem(25);
    left: rem(32);
  }

  &.-sub {
    display: none;
    z-index: 101;
  }

  /*　上に上がる動き　*/

