}

.l-header__logo {
  .l-header__logoLink {
    figure {
      picture {
        img {
          width: 100%;
        }
      }
    }
  }
}

.l-header__logoLink--subpage {
  display: none;
}

.l-header__right {
  @include responsive(lg2) {
    width: 50%;
  }
}

.l-header__button {
  display: none;

  @include responsive(lg2) {
    display: flex;
    position: absolute;
    top: rem(50);
    right: rem(115);
    z-index: 101;
  }
}

.l-header__buttonLink {
  @include responsive(lg2) {
    display: flex !important;
    justify-content: center;
    align-items: center;
    color: #fff;
    padding: rem(14) 0;
    width: rem(126);
  }

  &.-recruit {
    background: $color-text;
    font-family: $base-font-family;
    font-size: rem(14);
    font-weight: 700;
    letter-spacing: 0em;
    line-height: calc(14 / 14);
    margin-right: rem(22);
  }

  &.-entry {
    background: $color-sub;
    font-family: $second-font-family;
    font-size: rem(14);
    font-weight: 400;
    letter-spacing: 0em;
    line-height: calc(14 / 14);
    transition: 0.3s ease-in-out;
  }

  &.-entry:hover {
    background: $color-accent;
  }
}
