// ===========================
// color
// ===========================

// 基本カラー
$color-accent: #988c2c;
$color-sub: #afa86f;
$color-text: #222222;
$color-background: #faf7f1;

// ===========================
// font
// ===========================
$base-font-family: "Noto Sans JP";
// regular → 400
// medium → 500
// bold → 700

$second-font-family: Viga;
// regular → 400
// bold → 600

// ===========================
// config
// ===========================

// ブレイクポイント(モバイルファースト)
$breakpoints: (
  "xs": "screen and (min-width: 369px)",
  "sm": "screen and (min-width: 576px)",
  "md": "screen and (min-width: 769px)",
  "lg": "screen and (min-width: 1024px)",
  "lg2": "screen and (min-width: 1025px)",
  "xl": "screen and (min-width: 1280px)",
  "xxl": "screen and (min-width: 1440px)"
) !default;

$xs: 369px;
$sm: 576px;
$md: 769px;
$lg: 1024px;
$lg2: 1025px;
$xl: 1280px;
$xxl: 1440px;
