/* p-blog
----------------------------------------- */
@use "../../global" as *;

.p-blog {
    &__list {
        @include responsive(md) {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
        }

        @include responsive(lg2) {
            display: flex;
            flex-wrap: wrap;
            margin-top: rem(38);
        }
    }
    &__media {
        display: flex;
        justify-content: center;
        flex-direction: row-reverse;
        max-width: 100%;
        margin-top: rem(9);

        @include responsive(sm) {
            margin-top: rem(18);
        }

        @include responsive(md) {
            width: rem(452);
            height: rem(180);
        }

        @include responsive(lg2) {
            justify-content: center;
            width: rem(452);
            height: rem(180);
            margin-top: rem(23);
        }

        // &:nth-child(odd) {
        //     @include responsive(lg2) {
        //         margin-right: rem(52);
        //     }
        // }

        &:first-child {
            @include responsive(lg2) {
                margin-top: rem(23);
            }
        }
    }

    &__body {
        display: flex;
        justify-content: space-between;
        flex-flow: column;
        flex: 1 1 60%;
        background: $color-background;
        border-radius: 0 10px 10px 0;
        padding: rem(9) rem(14) rem(5) rem(18);

        @include responsive(lg2) {
            padding: rem(20) rem(23);
        }
    }

    &__head {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
        padding-left: rem(18);
        position: relative;

        @include responsive(lg2) {
            font-size: rem(14);
        }

        &::before {
            content: "";
            width: rem(12);
            height: rem(14);
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            background: url("../images/home/blog_icon.svg");
        }
    }

    &__txt {
        text-decoration: underline;
        text-decoration-color: $color-text;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(21 / 14);

        @include responsive(lg2) {
            font-size: rem(18);
            margin-top: rem(23);
        }
    }

    &__day {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
        margin-top: rem(12);
    }

    &__img {
        flex: 1 1 31%;
        border-radius: 10px 0 0 10px;
        overflow: hidden;

        @include responsive(lg2) {
            flex: 1 1 29%;
        }
        img {
            width: 100%;
            height: auto;
            max-height: 180px;
            object-fit: cover;
        }
    }
}

/*==============================================================
.p--blog-nav
==============================================================*/
.p-blog-nav {
    margin-top: rem(16);

    @include responsive(md) {
        margin-top: rem(98);
    }

    &__list {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__link {
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid $color-text;
        border-radius: 50%;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(14 / 14);
        width: rem(40);
        height: rem(40);

        @include responsive(md) {
            width: rem(58);
            height: rem(58);
        }

        &.is-active {
            background: $color-text;
            color: #fff;
        }
    }

    &__item:not(:last-child) {
        margin-right: rem(14);

        @include responsive(md) {
            margin-right: rem(32);
        }
    }
}
