/* link
----------------------------------------- */
@use "../global" as *;

.c-link {
    display: block;
    font-family: $second-font-family;
    font-size: rem(16);
    font-weight: 400;
    letter-spacing: 0em;
    line-height: calc(16 / 16);
    text-decoration: underline !important;
    text-decoration-color: #222222;
    padding-left: rem(69);
    position: relative;
}

.c-link::before {
    // position: absolute;
    // content: "";
    // width: 1em;
    // height: 1em;
    // right: -1em;
    // top: 0.3em;
    // background: url(画像のバスを入れる) center center/contain no-repeat;
    content: "";
    border: 1px solid $color-text;
    border-radius: 50%;
    background: #fff;
    width: 4.3rem;
    height: 4.3rem;
    transform: translateY(-50%);
    position: absolute;
    top: 0px;
    right: 30px;
}
