/* home-introduction
----------------------------------------- */
@use "../../global" as *;

.p-home-introduction {
    &__track {
        position: relative;
    }

    &__textWrapper {
        display: flex;
        flex-direction: column;
        width: 100%;
        text-align: center;
        position: absolute;
        top: 77%;
        left: 50%;
        transform: translateX(-50%);

        @include responsive(lg2) {
            top: 69%;
        }
    }
    &__text {
        display: inline-block;
        background: #fff;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(24);
        font-weight: 700;
        letter-spacing: 0em;
        width: fit-content;
        padding: rem(12) rem(11);
        margin: 0 auto;

        @include responsive(lg2) {
            font-size: clamp(3.6rem, 3.459rem + 0.376vw, 4rem);
            padding: rem(12) rem(14);
        }
    }

    &__text:nth-child(2) {
        margin-top: rem(8);

        @include responsive(lg2) {
            margin-top: rem(10);
        }
    }
}

/*==============================================================
Splide
==============================================================*/
// .p-home-introduction {
.splide-slider {
    &.l-section {
        padding: rem(41) 0 0 0;

        @include responsive(lg2) {
            padding: rem(101) 0 0 0;
        }
    }
}
// }

.p-home-introduction {
    .splide__track {
        padding-left: 14% !important;

        @include responsive(sm) {
            padding-left: 33% !important;
        }

        @include responsive(md) {
            padding-left: 37% !important;
        }

        @include responsive(lg2) {
            padding-left: 31% !important;
        }
    }
}

.p-home-introduction {
    &__inner {
        .splide {
            .splide__track {
                .splide__list {
                    .splide__slide {
                        border-radius: rem(60);
                        width: rem(271) !important;
                        height: rem(210) !important;
                        overflow: hidden;

                        @include responsive(lg2) {
                            width: rem(506) !important;
                            height: rem(393) !important;
                        }
                        figure {
                            @include img(271, 210);

                            @include responsive(lg2) {
                                @include img(300, 250);
                            }
                            img {
                                position: absolute;
                                top: 0;
                                right: 0;
                                bottom: 0;
                                left: 0;
                                width: 100%;
                                height: 100%;
                                margin: auto;
                                font-family: "object-fit: cover;";
                                object-fit: cover;
                            }
                        }
                    }
                }
            }
        }
    }
}

.splide__arrow {
    display: none !important;
}

.splide__pagination__page {
    display: none !important;
}
