/* staff-detail
----------------------------------------- */
@use "../../global" as *;

body.staff-detail {
    .p-mv {
        background: $color-background;
        margin-top: rem(60);

        @include responsive(lg2) {
            margin-top: rem(118);
        }
    }

    .wrap {
        overflow-x: initial;
    }
}

.p-staff-detail {
    &__contentInner {
        @include responsive(lg2) {
            display: flex;
            justify-content: center;
        }
    }

    &__contentWrapper {
        @include responsive(lg2) {
            width: 73%;
            margin-right: rem(54);
        }
    }
    &__item {
        @include responsive(md) {
            padding: rem(42) 0;
        }
    }
    &__img {
        @include responsive(lg2) {
            display: flex;
            justify-content: center;
            flex-direction: row-reverse;
            align-items: center;
        }
    }
    &__fig {
        display: none;

        @include responsive(lg2) {
            display: block;
        }
    }

    &__textBox {
        padding: rem(20) rem(16) rem(20) rem(16);

        @include responsive(md) {
            padding: rem(20) rem(40) rem(20) rem(40);
        }

        @include responsive(lg2) {
            width: 42%;
            padding: 0;
            margin-right: rem(85);
        }
    }

    &__catch {
        background: #fff;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(24);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(24 / 24);
        width: fit-content;
        padding: rem(3) rem(7);

        @include responsive(md) {
            font-size: rem(36);
        }
    }

    &__subCatch {
        background: #fff;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(18);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(18 / 18);
        width: fit-content;
        padding: rem(3) rem(7);
        margin-top: rem(7);

        @include responsive(md) {
            font-size: rem(24);
            margin-top: rem(18);
        }
    }

    &__content {
        margin-top: rem(20);

        @include responsive(md) {
            margin-top: rem(31);
        }
    }

    &__title {
        color: $color-accent;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(12 / 12);

        @include responsive(md) {
            font-size: rem(14);
        }
    }

    &__name {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(24);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(24 / 24);
        margin-top: rem(10);

        @include responsive(md) {
            font-size: rem(24);
        }
    }

    &__name span {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        padding-left: rem(14);
        letter-spacing: 0em;
        line-height: calc(12 / 12);

        @include responsive(md) {
            font-size: rem(16);
            padding-left: rem(20);
        }
    }

    &__comment {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(18 / 12);
        margin-top: rem(11);

        @include responsive(sm) {
            width: 74%;
        }

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(28.8 / 16);
            width: 100% !important;
            margin-top: rem(13);
        }
    }

    &__contentItem {
        margin-top: rem(23);
        position: relative;

        &::before {
            @include responsive(lg2) {
                position: absolute;
                content: "";
                width: rem(216);
                height: rem(1);
                left: rem(-231);
                top: rem(13);
                background: $color-text;
            }
        }
    }

    &__contentTitle {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(18);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(18 / 18);
        padding-bottom: rem(24);

        @include responsive(md) {
            font-size: rem(24);
            padding-bottom: rem(50);
        }
    }

    &__text {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(22 / 14);

        @include responsive(lg2) {
            font-size: rem(16);
            line-height: calc(28 / 16);
        }
    }
}

/*==============================================================
.p-staff-detail__nav
==============================================================*/
.p-staff-detail {
    &__nav {
        display: none;

        @include responsive(lg2) {
            display: block;
            position: sticky;
            top: rem(94);
            height: 100vh;
            padding-top: rem(24);
        }
    }

    &__toc {
        color: #666;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(21 / 14);
    }

    &__navList {
        padding-top: rem(13);
    }

    &__navItem:not(:first-child) {
        margin-top: rem(16);
    }

    &__link {
        color: #999;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(21 / 14);
    }

    &__link.is-active {
        color: $color-text;
    }
}

/*==============================================================
.p-staff-member
==============================================================*/
.p-staff-member {
    &__list {
        @include responsive(lg2) {
            display: flex;
            justify-content: center;
            padding-top: rem(64);
        }
    }
    &__item {
        // width: rem(246) !important;
        // height: rem(311) !important;
        position: relative;

        // @include responsive(lg2) {
        //     width: rem(300) !important;
        //     height: rem(379) !important;
        // }

        // &:nth-child(odd) {
        //     transform: translateY(25px);
        // }
    }

    &__item:not(:last-child) {
        @include responsive(lg2) {
            margin-right: rem(44);
        }
    }

    &__textBox {
        position: absolute;
        top: rem(263);
        left: 0;

        @include responsive(lg2) {
            top: rem(332);
        }
    }

    &__comment {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(16);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(16 / 16);

        @include responsive(lg2) {
            font-size: rem(20);
        }

        p {
            display: inline-block;
            background: $color-sub;
            padding: rem(5) rem(7);
        }

        p:nth-child(2) {
            margin-top: rem(8);

            @include responsive(lg2) {
                margin-top: rem(6);
            }
        }
    }

    &__title {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
        margin-top: rem(10);

        @include responsive(lg2) {
            font-size: rem(16);
            margin-top: rem(14);
        }
    }

    &__title span:nth-of-type(2) {
        padding-left: rem(6);
    }

    &__name {
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(20 / 20);
        margin-top: rem(14);

        @include responsive(lg2) {
            font-size: rem(24);
            margin-top: rem(16);
        }
    }
}
