/* about
----------------------------------------- */
@use "../../global" as *;

/*==============================================================
p-about
==============================================================*/
.p-about {
    &__list {
        margin-top: rem(36);

        @include responsive(md) {
            margin-top: rem(86);
        }
    }

    &__item {
        background: $color-background;
        border-radius: 10px;
        padding: rem(14) rem(12);

        @include responsive(md) {
            padding: rem(30) rem(80);
        }
    }

    &__item:not(:first-child) {
        margin-top: rem(20);

        @include responsive(md) {
            margin-top: rem(36);
        }
    }

    &__titleWrapper {
        display: flex;
        align-items: center;
    }

    &__title {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(16);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(16 / 16);
        width: fit-content;

        @include responsive(md) {
            font-size: rem(20);
            line-height: calc(20 / 20);
        }
    }

    &__num {
        display: flex;
        justify-content: center;
        align-items: center;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(16);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(16 / 16);
        border: 1px solid $color-text;
        border-radius: 50%;
        width: rem(32);
        height: rem(32);
        margin-right: rem(14);

        @include responsive(md) {
            font-size: rem(18);
            width: rem(48);
            height: rem(48);
            margin-right: rem(21);
        }
    }

    &__text {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 12);
        margin-top: rem(20);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(24 / 16);
            // margin-top: rem(35);
        }
    }
}

/*==============================================================
p-about-message
==============================================================*/
.p-about-message {
    &__heading {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(20 / 12);
        margin-top: rem(20);

        @include responsive(md) {
            font-size: rem(32);
            line-height: calc(48 / 32);
            margin-top: 0;
        }

        @include responsive(lg2) {
            position: absolute;
            top: rem(18);
            right: rem(-385);
        }
    }

    &__wrapper {
        @include responsive(md) {
            margin-top: rem(109);
        }

        @include responsive(lg2) {
            display: flex;
            justify-content: center;
        }
    }

    &__img {
        img {
            width: 100%;
            object-fit: cover;
        }
    }

    &__box--left {
        margin-top: rem(20);

        @include responsive(md) {
            margin-top: 0;
        }

        @include responsive(lg2) {
            position: relative;
            width: 49.4rem;
            margin-right: 1.9rem;
        }
    }

    &__box--right {
        margin-top: rem(30);

        @include responsive(lg2) {
            width: 50%;
            padding-top: rem(176);
        }

        figure {
            margin-top: rem(20);

            @include responsive(md) {
                margin-top: rem(21);
            }
        }
    }

    &__text {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 12);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(32 / 16);
        }
    }

    &__text:not(:first-of-type) {
        margin-top: rem(20);

        @include responsive(md) {
            margin-top: rem(30);
            // margin-bottom: rem(31);
        }
    }
}

/*==============================================================
p-about-business
==============================================================*/
.p-about-business {
    &__contentWrapper {
        @include responsive(md) {
            margin-top: rem(83);
        }
    }
    &__content {
        padding-top: rem(20);
        padding-bottom: rem(20);

        @include responsive(md) {
            display: flex;
            justify-content: flex-end;
            flex-direction: row-reverse;
            padding-top: 0;
            padding-bottom: rem(48);
        }

        &:first-of-type {
            // padding-bottom: rem(47);
        }

        &:not(:first-of-type) {
            @include responsive(md) {
                margin-top: 49px;
                padding-bottom: 50px;
            }
            // padding: rem(47) 0;
        }

        &:not(:last-of-type) {
            border-bottom: 1px solid $color-sub;
        }
    }

    &__body {
        @include responsive(md) {
            width: 45%;
            padding-top: rem(22);
        }
    }

    &__head {
        color: $color-accent;
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(30 / 20);

        @include responsive(md) {
            font-size: rem(32);
            line-height: calc(32 / 32);
        }
    }

    &__intro {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(18);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(24 / 18);
        margin-top: rem(16);

        @include responsive(md) {
            font-size: rem(24);
            line-height: calc(36 / 24);
            margin-top: rem(33);
        }
    }

    &__txt {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 12);
        margin-top: rem(10);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(24 / 16);
            margin-top: rem(34);
        }
    }

    &__img {
        margin-top: rem(16);

        @include responsive(md) {
            width: 46.6%;
            margin-top: initial;
            margin-right: rem(48);
        }
        picture {
            img {
                width: 100%;
                height: auto;
                object-fit: cover;
            }
        }
    }
}

