/* container
----------------------------------------- */
@use "../global" as *;

.l-container {
  width: 100%;
  max-width: 1360px;
  margin: 0 auto;
  padding: 0 rem(20);

  @include responsive(md) {
    padding: 0 rem(40);
  }

  @include responsive(lg2) {
    padding: 0 rem(190);
  }
}

/*==============================================================
p-about
==============================================================*/
.p-about {
  .l-container {
    padding-top: rem(40);

    @include responsive(md) {
      padding-top: rem(66);
    }
  }

  &-message {
    .l-container {
      padding-top: rem(40);

      @include responsive(md) {
        margin-top: rem(86);
      }
    }
  }

  &-business {
    .l-container {
      padding-top: rem(80);

      @include responsive(md) {
        padding-top: rem(113);
      }
    }
  }

  &-companyInfo {
    .l-container {
      padding-top: rem(80);
      padding-bottom: rem(80);

      @include responsive(md) {
        padding-top: rem(114);
        padding-bottom: rem(139);
      }
    }
  }
}

/*==============================================================
p-staff-detail
==============================================================*/

.p-staff-detail {
  .l-container {
    padding-top: rem(40);
    padding-bottom: rem(40);

    @include responsive(lg2) {
      padding-top: rem(80);
      padding-bottom: rem(130);
    }
  }
}

.p-staff-member {
  .l-container {
    @include responsive(lg2) {
      padding: rem(22) rem(160) rem(229);
    }
  }
}

/*==============================================================
p-blog
==============================================================*/
.p-blog {
  .l-container {
    padding-top: rem(40);
    padding-bottom: rem(40);

    @include responsive(lg2) {
      padding: rem(68) rem(176) rem(140);
    }
  }
}

/*==============================================================
p-blog-detail
==============================================================*/

.p-blog-detail {
  .l-container {
    padding-top: rem(34);
    padding-bottom: rem(80);

    @include responsive(md) {
      padding: rem(34) rem(280) rem(139);
    }
  }
}
