/* home-benefits
----------------------------------------- */
@use "../../global" as *;

.p-home-benefit {
    &__container {
        margin-top: rem(54);

        @include responsive(md) {
            display: flex;
            justify-content: center;
            width: 85%;
            margin: rem(100) auto 0;
        }

        @include responsive(lg2) {
            margin-top: rem(114);
        }
    }

    &__circle {
        transform: translateY(-50%);
        position: absolute;
        top: 74%;
        right: rem(12);

        @include responsive(md) {
            top: 80%;
            right: rem(6);
        }

        @include responsive(lg2) {
            top: 79.6%;
            right: rem(20);
        }
    }
}

.p-card {
    width: 100%;
    position: relative;

    &.-traning {
        @include responsive(md) {
            margin-right: rem(50);
        }

        @include responsive(lg2) {
            margin-right: rem(94);
        }
    }

    &.-employee {
        margin-top: rem(140);

        @include responsive(md) {
            margin-top: initial;
        }

        // @include responsive(lg2) {
        //     margin-top: initial;
        // }
    }

    &__link {
        display: block;
        background: $color-background;
        border-radius: 50%;
        width: rem(251);
        height: rem(251);
        margin: 0 auto;
        position: relative;

        @include responsive(lg2) {
            width: rem(430);
            height: rem(430);
        }

        &.-traning::before {
            content: "Traning And Career";
            display: inline-block;
            font-family: $second-font-family;
            color: $color-text;
            font-size: rem(14);
            font-weight: 400;
            letter-spacing: 2px;
            line-height: calc(14 / 14);
            transform: rotate(90deg);
            position: absolute;
            height: rem(14);
            top: rem(66);
            right: rem(-58);
            z-index: 2;

            @include responsive(md) {
                top: rem(80);
                right: rem(-72.2);
            }

            @include responsive(lg2) {
                font-size: rem(24);
                letter-spacing: 3.6px;
                top: rem(140);
                right: rem(-110);
            }
        }

        &.-traning::after {
            // content: "";
            // border: 1px solid #222222;
            // border-radius: 50%;
            // background: #fff;
            // width: rem(43);
            // height: rem(43);
            // transform: translateY(-50%);
            // position: absolute;
            // top: 74%;
            // right: rem(12);

            // @include responsive(md) {
            //     top: 80%;
            //     right: rem(6);
            // }

            // @include responsive(lg2) {
            //     width: rem(59);
            //     height: rem(59);
            //     top: 79.6%;
            //     right: rem(20);
            // }
        }

        &.-employee::before {
            content: "employee benefits";
            display: inline-block;
            font-family: $second-font-family;
            color: $color-text;
            font-size: rem(14);
            font-weight: 400;
            letter-spacing: 2px;
            line-height: calc(14 / 14);
            transform: rotate(90deg);
            position: absolute;
            height: rem(14);
            top: rem(67);
            right: rem(-54);
            z-index: 2;

            @include responsive(md) {
                top: rem(80);
                right: rem(-72.2);
            }

            @include responsive(lg2) {
                font-size: rem(24);
                letter-spacing: 3.6px;
                top: rem(130);
                right: rem(-102);
            }
        }

        // &.-employee::after {
        //     content: "";
        //     border: 1px solid #222222;
        //     border-radius: 50%;
        //     background: #fff;
        //     width: 4.3rem;
        //     height: 4.3rem;
        //     transform: translateY(-50%);
        //     position: absolute;
        //     top: 74%;
        //     right: rem(12);

        //     @include responsive(md) {
        //         top: 80%;
        //         right: rem(6);
        //     }

        //     @include responsive(lg2) {
        //         width: rem(59);
        //         height: rem(59);
        //         top: 79.6%;
        //         right: rem(20);
        //     }
        // }
    }
}

.p-card.-employee {
    .p-card__link {
        .p-card__img {
            width: 78.18%;
        }
    }
}

.p-card__img {
    width: 57.18%;
    z-index: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    img {
        object-fit: cover;
        height: auto;
        width: 100%;
        transition: transform 0.8s;
    }
}

.p-card__body {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 89%;
    left: 50%;
    transform: translateX(-50%);

    @include responsive(md) {
        top: 93%;
    }

    @include responsive(lg2) {
        top: 89%;
    }
}

@media screen and (min-width: 769px) {
    .p-card:hover > a > figure > img {
        transform: scale(1.2);
        transition: transform 0.8s;
    }
}

.p-card__title {
    display: inline-block;
    background: $color-sub;
    font-family: $base-font-family;
    font-size: rem(24);
    font-weight: 700;
    letter-spacing: 0em;
    text-align: center;
    padding: rem(5);

    @include responsive(lg2) {
        font-size: rem(32);
    }
}

.p-card__text {
    font-family: $base-font-family;
    font-size: rem(16);
    font-weight: 400;
    letter-spacing: 0em;
    line-height: calc(24 / 16);
    text-align: left;
    margin-top: rem(20);

    @include responsive(lg2) {
        margin-top: rem(40);
    }
}

.c-link__wrap.-benefits {
    position: absolute;
    top: 74%;
    right: 8%;
}
