/* home-subCatch
----------------------------------------- */
@use "../../global" as *;

.p-home-subCatch {
    &__bg {
        background-image: url("../images/home/map_img.png");
        background-position: top right;
        background-repeat: no-repeat;
        background-size: contain;
        width: rem(350);
        margin: 0 auto;

        @include responsive(lg2) {
            width: rem(1024);
            margin: 0 auto;
        }
    }

    &__container {
        text-align: center;
    }

    &__text {
        @include font(18, 45, 700, 0);

        @include responsive(lg2) {
            font-size: rem(36);
            font-weight: 700;
            letter-spacing: 0em;
            line-height: calc(64.8 / 36);
        }

        &.-bottom {
            margin-top: rem(39);

            @include responsive(lg2) {
                margin-top: rem(52);
            }
        }
    }
}
