/* menu
----------------------------------------- */
@use "../global" as *;

.l-menu {
  position: fixed;
  top: 0;
  right: 0;
  display: block;
  visibility: hidden;
  overflow-y: scroll;
  width: 100%;
  height: 100vh;
  padding: rem(90) rem(12) rem(40);
  background-color: $color-background;
  opacity: 0;
  transition: all 0.5s ease;

  @include responsive(md) {
    padding: rem(90) rem(40) rem(40);
  }

  @include responsive(lg2) {
    padding: 0;
  }

  &.is-active {
    z-index: 100;
    visibility: visible;
    opacity: 1;
    animation-duration: 0.5s;
  }

  &__logo {
    position: absolute;
    top: rem(8);
    z-index: 102;

    @include responsive(lg2) {
      position: initial;
    }
  }

  &__wrapper {
    display: flex;

    @include responsive(lg2) {
      justify-content: right;
      width: 100%;
      position: absolute;
      top: 28.8%;
      right: 3.4%;
    }
  }

  &__nav {
    width: 100%;

    @include responsive(lg2) {
      border-left: 1px solid #afa86f;
      width: 45.4%;
      padding-left: rem(63);
    }
  }

  &__logo {
    @include responsive(lg2) {
      display: flex !important;
      align-items: center;
      width: 17.9%;
      margin-right: rem(63);
    }
  }

  &__logoLink {
    display: block;
    width: 100%;

    & figure {
      picture {
        img {
          width: 100%;
        }
      }
    }
  }

  &__list {
    transition: opacity 0.8s cubic-bezier(0.4, 0, 0.2, 1);
    @include responsive(lg2) {
      display: flex;
      margin-right: rem(50);
      @include flex(flex-start, center, row);
    }
  }

  &__subList--left {
    @include responsive(lg2) {
      width: 47.2%;
      margin-right: rem(30);
    }
  }

  &__subList--right {
    @include responsive(lg2) {
      width: 47.2%;
    }
  }

  &__item {
    cursor: pointer;
    border-top: 1px solid #afa86f;

    @include responsive(lg2) {
      padding: rem(23) 0;
    }

    &.-border-bottom {
      border-bottom: 1px solid #afa86f;
    }
  }

  &__link {
    display: block;
    @include font(18, 20, 600, 100, rem);
    padding: rem(11) 0;

    @include responsive(lg2) {
      @include font(16, 16, 400, 150, rem);
      padding: 0;
      position: relative;
      transition: opacity 0.5s ease;
    }
  }

  &__title {
    font-size: rem(18);
    font-family: $second-font-family;
    letter-spacing: 0.1px;

    @include responsive(lg2) {
      font-size: rem(32);
    }
  }

  &__subTitle {
    font-size: rem(12);
    font-family: $base-font-family;
    margin-top: rem(9);
    letter-spacing: 0.1px;

    @include responsive(lg2) {
      font-size: rem(16);
      font-weight: 700;
      margin-top: rem(11);
    }
  }
}

/* l-menu-button */
.l-menu-button {
  width: 100%;
  max-width: 300px;
  margin: rem(50) 0;

  @include responsive(lg2) {
    margin: rem(50) 0 0 0;
  }

  &__list {
    @include responsive(lg2) {
      display: flex;
    }
  }

  &__item {
    cursor: pointer;
    width: 70.65%;
  }

  &__link {
    display: block;
    padding: rem(12) rem(21.5);
    color: #fff;
    font-family: $second-font-family;
    text-align: center;
    @include font(14, 14, 700, 100, rem);
    transition:
      color 0.5s ease,
      background-color 0.5s ease;

    &.-entry {
      background: $color-sub;
      border: 2px solid $color-sub;
      margin-top: rem(13);

      @include responsive(lg2) {
        margin-top: 0;
      }

      &:hover {
        background: #fff;
        color: #ebc110;
      }
    }

    &.-recruit {
      background: $color-text;
      border: 2px solid $color-text;
      @include responsive(lg2) {
      }
      &:hover {
        background: #fff;
        color: $color-text;
      }
    }
  }
}
