/* c-button
----------------------------------------- */
@use "../global" as *;

.c-button {
    border: 1px solid $color-text;
    width: rem(277);
    max-width: rem(355);
    margin: rem(39) auto 0;

    @include responsive(lg2) {
        width: rem(355);
        margin: rem(52) auto 0;
    }
}

.c-button a {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #fff;
    color: $color-text;
    font-family: $second-font-family;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 1.8;
    padding: rem(15) rem(25);
    text-decoration: none;
    position: relative;
    transition: 0.3s ease-in-out;

    @include responsive(lg2) {
        font-size: rem(20);
        padding: rem(22) rem(25);
    }
}
.c-button a:hover {
    background: $color-text;
    color: #fff;
}
.c-button a:after {
    content: "";
    width: 13px;
    height: 13px;
    border-top: 3px solid $color-text;
    border-right: 3px solid $color-text;
    transform: rotate(45deg) translateY(-50%);
    position: absolute;
    top: 50%;
    right: rem(25);
    border-radius: 1px;
    transition: 0.3s ease-in-out;

    @include responsive(lg2) {
        top: 48%;
        right: 3rem;
        width: 15px;
        height: 15px;
    }
}
.c-button a:hover:after {
    border-color: #fff;
}
