/* home-recruit
----------------------------------------- */
@use "../../global" as *;

.p-home-recruit {
    background: #faf7f1;

    &__wrapper {
        width: 100%;
        position: relative;
        @include responsive(lg2) {
            padding: 0 rem(34);
        }
    }
    &__list {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        margin-top: rem(28);

        @include responsive(lg2) {
            margin-top: rem(60);
        }
    }

    &__innerList {
        @include responsive(lg2) {
            display: flex;
        }
    }

    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        background: #fff;
        text-align: center;
        width: 100%;
        position: relative;

        &:not(:first-child) {
            margin-top: rem(19);

            @include responsive(lg2) {
                margin-top: 0;
            }
        }

        &:first-child {
            @include responsive(lg2) {
                margin-right: rem(16);
            }
        }

        &.-row {
            @include responsive(lg2) {
                margin-top: rem(21);
            }
        }
    }

    &__link {
        display: block;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(20 / 20);
        width: 100%;
        padding: rem(33) 0;

        @include responsive(lg2) {
            padding: rem(40) 0;
        }

        &::after {
            position: absolute;
            top: 40%;
            right: 2.6rem;
            width: 14px;
            height: 14px;
            transform: rotate(45deg);
            border-top: 4px solid #222222;
            border-right: 4px solid #222222;
            border-radius: 1px;
            content: "";
            transition: 0.3s ease-in-out;

            @include responsive(lg2) {
                top: 43%;
                right: 2.7rem;
            }
        }

        &.-company {
            @include responsive(lg2) {
                padding: rem(50) 0;
            }
        }
    }

    &__img {
        width: 100%;
        position: absolute;
        top: 50%;
        left: 0;
    }

    &__descWrapper {
        background: #fff;
        border-radius: 20px;
        padding: rem(23);
        margin-top: rem(220);
    }

    &__descText {
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(35 / 14);
        text-align: center;

        @include responsive(lg2) {
            font-size: rem(16);
            line-height: calc(24 / 16);
        }
    }
}
