/* footer
----------------------------------------- */
@use "../global" as *;

.l-footer {
    @include responsive(lg2) {
        padding-bottom: rem(64);
    }
    &__navItem {
        text-align: center;

        &:not(:first-child) {
            margin-top: rem(20);

            @include responsive(lg2) {
                margin-top: initial;
            }
        }
    }

    &__container {
        background: $color-background;
        padding: rem(168) 0 rem(24);
        max-width: 1312px;

        @include responsive(md) {
            padding: rem(168) 0 rem(24);
        }

        @include responsive(lg2) {
            border-radius: 0 0 20px 20px;
            padding: rem(198) rem(178) rem(60);
        }
    }

    &__detail {
        @include responsive(lg2) {
            display: flex;
            justify-content: center;
            flex-direction: row-reverse;
        }
    }

    &__navWrapper {
        @include responsive(lg2) {
            margin-top: rem(26);
        }
    }

    &__navList {
        @include responsive(lg2) {
            display: flex;
            flex-wrap: wrap;
            gap: rem(40) rem(22);
        }
    }

    &__navLink {
        display: block;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(14 / 14);

        @include responsive(lg2) {
            font-size: rem(16);
        }
    }

    &__snsList {
        display: flex;
        justify-content: center;
        margin-top: rem(55);

        @include responsive(lg2) {
            justify-content: right;
            margin-top: rem(61);
        }
    }

    &__snsItem:not(:last-child) {
        margin-right: rem(16);
    }

    &__logo {
        text-align: center;
        max-width: 251px;
        margin: rem(29) auto 0;

        @include responsive(lg2) {
            width: 100%;
            margin-top: 0;
            margin-right: rem(108);
        }
    }

    &__logoLink {
        display: block;
    }

    &__policy {
        text-align: center;
        margin-top: rem(40);

        @include responsive(lg2) {
            text-align: left;
            margin-top: rem(95);
        }
    }

    &__policyLink {
        color: #999999;
        font-family: $second-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
    }
}

.l-footer-recruit {
    background: #faf7f1;

    &__wrapper {
        width: 100%;
        position: relative;
        @include responsive(lg2) {
        }
    }
    &__list {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;

        @include responsive(lg2) {
            margin-top: rem(64);
        }
    }

    &__innerList {
        @include responsive(lg2) {
            display: flex;
        }
    }

    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        background: #fff;
        text-align: center;
        width: 100%;
        position: relative;

        &:not(:first-child) {
            margin-top: rem(19);

            @include responsive(lg2) {
                margin-top: 0;
            }
        }

        &:first-child {
            @include responsive(lg2) {
                margin-right: rem(16);
            }
        }

        &.-row {
            @include responsive(lg2) {
                margin-top: rem(21);
            }
        }
    }

    &__link {
        display: block;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(20 / 20);
        width: 100%;
        padding: rem(33) 0;

        @include responsive(lg2) {
        }

        &::after {
            position: absolute;
            top: 50%;
            right: rem(25);
            width: 13px;
            height: 13px;
            transform: rotate(45deg) translateY(-50%);
            border-top: 3px solid #222222;
            border-right: 3px solid #222222;
            border-radius: 1px;
            content: "";
            transition: 0.3s ease-in-out;
        }
    }

    &__img {
        width: 100%;

        & img {
            width: 100%;
            height: rem(250);
            object-fit: cover;

            @include responsive(lg2) {
                height: rem(488);
            }
        }
    }

    &__descWrapper {
        width: 100%;
        padding: 0 4.3%;
        position: absolute;
        top: 52%;
        left: 50%;
        transform: translateX(-50%);

        @include responsive(lg2) {
            width: 74.86%;
            top: rem(352);
        }
    }

    &__descInner {
        background: #fff;
        border-radius: 20px;
        padding: rem(26) 0 rem(28) 0;

        @include responsive(lg2) {
            padding: rem(50) rem(23) rem(50) rem(23);
        }
    }

    &__descText {
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(35 / 14);
        text-align: center;

        @include responsive(lg2) {
            font-size: rem(16);
            line-height: calc(24 / 16);
        }
    }
}

//c-button -entry
.c-button.-entry {
    border: none;
    max-width: 556px;
    width: rem(200);
    margin-top: rem(22);

    @include responsive(lg2) {
        width: 100%;
        margin-top: rem(32);
    }
}

.c-button.-entry a {
    background: $color-sub;
    padding: rem(13) 0;

    @include responsive(lg2) {
        font-size: rem(16);
        padding: rem(20) 0;
    }

    &:hover {
        color: #fff;
        background: $color-text;
    }
}
