/* blog-detail
----------------------------------------- */
@use "../../global" as *;

body.blog-detail {
    .c-breadcrumbs {
        margin-top: rem(60);

        @include responsive(md) {
            margin-top: rem(80);
        }

        @include responsive(lg) {
            margin-top: rem(60);
        }

        @include responsive(lg2) {
            margin-top: rem(118);
        }
    }
}

/*==============================================================
.p--blog-detail
==============================================================*/
.p-blog-detail {
    background: $color-background;

    &__body {
        background: #fff;
        border-radius: 10px;
        padding: rem(30) rem(20);

        @include responsive(md) {
            padding: rem(36) rem(40) rem(23);
        }
    }
    &__index {
        display: inline-block;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
        padding-left: rem(18);
        position: relative;

        @include responsive(lg2) {
            font-size: rem(14);
        }

        &::before {
            content: "";
            width: rem(12);
            height: rem(14);
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            background: url("../images/home/blog_icon.svg");
        }
    }

    &__day {
        display: inline-block;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(12 / 12);

        @include responsive(md) {
            font-size: rem(14);
            line-height: calc(14 / 14);
            padding-left: rem(13);
        }
    }

    &__txt {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(21 / 14);
        margin-top: rem(14);

        @include responsive(lg2) {
            font-size: rem(32);
            margin-top: rem(25);
            line-height: calc(32 / 32);
        }
    }

    &__intro {
        margin-top: rem(20);

        @include responsive(md) {
            margin-top: rem(41);
        }
    }

    &__introText {
        margin-top: rem(40);

        @include responsive(md) {
            margin-top: rem(45);
        }
    }

    &__img {
        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }

    &__introText {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 14);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(28 / 16);
        }
    }

    &__contentList {
        @include responsive(md) {
            margin-top: rem(25);
        }
    }

    &__contentItem:not(:first-child) {
        @include responsive(md) {
            margin-top: rem(30);
        }
    }

    &__contentTitle {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(20 / 20);

        @include responsive(md) {
            font-size: rem(24);
            line-height: calc(24 / 24);
        }
    }

    &__contentText {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 14);
        margin-top: rem(30);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(28 / 16);
            margin-top: rem(50);
        }
    }
}

/*==============================================================
.p--blog-detail__nav
==============================================================*/
.p-blog-detail {
    &__nav {
        background: #f0f0f0;
    }

    &__navList {
        position: relative;
        @include responsive(md) {
            display: flex;
            justify-content: space-around;
            align-items: center;
            padding: 0 rem(77);
        }
    }

    &__navList:before {
        position: absolute;
        content: "";
        background: $color-text;
        width: rem(1);
        height: rem(56);
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }

    &__navItem:first-child {
        border-bottom: 1px solid $color-text;

        @include responsive(md) {
            border-bottom: none;
        }
    }

    &__navLink {
        position: relative;
        display: block;
        padding: rem(10) rem(10);

        @include responsive(md) {
            padding: rem(33) 0;
        }
    }

    &__navLink.-prev {
        padding-left: rem(50);
    }

    &__navLink.-prev::before {
        position: absolute;
        content: "";
        width: rem(24);
        height: rem(24);
        top: 50%;
        left: rem(5);
        transform: translateY(-50%);
        background: url(../images/blog/details/arrow02-left.svg);
        background-size: contain;
    }

    &__navLink.-next {
        text-align: right;
        padding-right: rem(50);

        @include responsive(md) {
            text-align: left;
        }
    }

    &__navLink.-next::before {
        position: absolute;
        content: "";
        width: rem(24);
        height: rem(24);
        top: 50%;
        right: rem(5);
        transform: translateY(-50%);
        background: url(../images/blog/details/arrow02-right.svg);
        background-size: contain;
    }

    &__navText {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(14 / 14);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(16 / 16);
        }
    }
    &__navDay {
        display: inline-block;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(12 / 12);

        @include responsive(sm) {
        }
    }
}
