/* column
----------------------------------------- */
.c-column2 {
  display: grid;
  gap: 10 10;
  grid-template-columns: repeat(2, 1fr);
}
@media screen and (max-width: 767px) {
  .c-column2 {
    grid-template-columns: repeat(1, 1fr);
    gap: 10;
  }
}

.c-column2--gap60 {
  gap: 10;
}
@media screen and (max-width: 767px) {
  .c-column2--gap60 {
    gap: 10;
  }
}

@media screen and (max-width: 767px) {
  .c-column2--gapSp20 {
    gap: 10;
  }
}

.c-column3 {
  display: grid;
  gap: 10;
  grid-template-columns: repeat(3, 1fr);
}
@media screen and (max-width: 767px) {
  .c-column3 {
    grid-template-columns: repeat(1, 1fr);
  }
}

.c-column4 {
  display: grid;
  gap: 10;
  grid-template-columns: repeat(4, 1fr);
}
@media screen and (max-width: 767px) {
  .c-column4 {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (max-width: 767px) {
  .c-column4--sp2 {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* headline
----------------------------------------- */
.c-headline {
  border-left: 1px solid black;
  font-size: 1.6rem;
  font-weight: 600;
  color: black;
  padding-left: 1.6rem;
}
@media screen and (min-width: 769px) {
  .c-headline {
    padding-left: 1.6rem;
    font-size: 1.6rem;
  }
}

/* hooverUnderline
----------------------------------------- */
.c-hoverUnderline {
  position: relative;
  display: inline-block;
}

.c-hoverUnderline::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: black;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

@media screen and (min-width: 768px) {
  .c-hoverUnderline:hover::after {
    opacity: 1;
  }
}
/* imgHover
----------------------------------------- */
.c-imgHover {
  position: relative;
}

.c-imgHover img {
  width: 100%;
  transition: 0.5s opacity;
}

.c-imgHover img:nth-of-type(2) {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  transition: 0.5s opacity;
}

@media screen and (min-width: 768px) {
  .c-imgHover:hover img:nth-of-type(1) {
    opacity: 0;
    transition: 0.5s opacity;
  }
  .c-imgHover:hover img:nth-of-type(2) {
    opacity: 1;
    transition: 0.5s opacity;
  }
}
/* link
----------------------------------------- */
.c-link {
  display: block;
  font-family: Viga;
  font-size: 1.6rem;
  font-weight: 400;
  letter-spacing: 0em;
  line-height: 1;
  text-decoration: underline !important;
  text-decoration-color: #222222;
  padding-left: 6.9rem;
  position: relative;
}

.c-link::before {
  content: "";
  border: 1px solid #222222;
  border-radius: 50%;
  background: #fff;
  width: 4.3rem;
  height: 4.3rem;
  transform: translateY(-50%);
  position: absolute;
  top: 0px;
  right: 30px;
}

/* underBar
----------------------------------------- */
.c-underBar {
  position: relative;
  display: inline-block;
}
.c-underBar::before {
  background-color: #333;
  bottom: 0;
  content: "";
  height: 1px;
  left: 50%;
  opacity: 1;
  position: absolute;
  transform: translateX(-50%);
  transition: 0.5s;
  width: 100%;
}

@media screen and (min-width: 768px) {
  .c-underBar:hover:before {
    opacity: 0;
    transition: 0.5s all;
  }
}
