/* c-button
----------------------------------------- */
.c-button {
  border: 1px solid #222222;
  width: 27.7rem;
  max-width: 35.5rem;
  margin: 3.9rem auto 0;
}
@media screen and (min-width: 1025px) {
  .c-button {
    width: 35.5rem;
    margin: 5.2rem auto 0;
  }
}

.c-button a {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #fff;
  color: #222222;
  font-family: Viga;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 1.8;
  padding: 1.5rem 2.5rem;
  text-decoration: none;
  position: relative;
  transition: 0.3s ease-in-out;
}
@media screen and (min-width: 1025px) {
  .c-button a {
    font-size: 2rem;
    padding: 2.2rem 2.5rem;
  }
}

.c-button a:hover {
  background: #222222;
  color: #fff;
}

.c-button a:after {
  content: "";
  width: 13px;
  height: 13px;
  border-top: 3px solid #222222;
  border-right: 3px solid #222222;
  transform: rotate(45deg) translateY(-50%);
  position: absolute;
  top: 50%;
  right: 2.5rem;
  border-radius: 1px;
  transition: 0.3s ease-in-out;
}
@media screen and (min-width: 1025px) {
  .c-button a:after {
    top: 48%;
    right: 3rem;
    width: 15px;
    height: 15px;
  }
}

.c-button a:hover:after {
  border-color: #fff;
}

/* c-arrow
----------------------------------------- */
.c-circle {
  display: block;
  border: 1px solid #222222;
  border-radius: 50%;
  background: #222222;
  width: 4.3rem;
  height: 4.3rem;
}
@media screen and (min-width: 1025px) {
  .c-circle {
    width: 5.9rem;
    height: 5.9rem;
  }
}
.c-circle.-blog {
  background: #fff;
  border: 1px solid #fff;
  width: 5.9rem;
  height: 5.9rem;
}

.c-arrow {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
}
.c-arrow::before {
  content: "";
  display: block;
  left: 3px;
  width: 16px;
  height: 2px;
  background: #fff;
  position: absolute;
  top: 50%;
  left: 48%;
  transform: translate(-50%, -48%);
}
.c-arrow::after {
  content: "";
  display: block;
  left: 3px;
  width: 12px;
  height: 12px;
  border-top: 2px solid #fff;
  border-right: 2px solid #fff;
  border-radius: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
}
.c-arrow.-blog::before {
  background: #222222;
}
.c-arrow.-blog::after {
  border-top: 2px solid #222222;
  border-right: 2px solid #222222;
}

