/* swiper
----------------------------------------- */
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
a,
p,
span,
em,
small,
strong,
sub,
sup,
mark,
del,
ins,
strike,
abbr,
dfn,
blockquote,
q,
cite,
code,
pre,
ol,
ul,
li,
dl,
dt,
dd,
div,
section,
article,
main,
aside,
nav,
header,
hgroup,
footer,
img,
figure,
figcaption,
address,
time,
audio,
video,
canvas,
iframe,
details,
summary,
fieldset,
form,
input,
textarea,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

* {
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}

a,
a:visited,
a:link,
a:active {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

article,
aside,
footer,
header,
nav,
section,
main {
  display: block;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

ol li,
ul li {
  list-style: none;
}

img,
iframe,
video {
  max-width: 100%;
}

img {
  height: auto;
  border: none;
  vertical-align: top;
}

br {
  line-height: 1;
}

blockquote,
q {
  quotes: none;
}

blockquote::after,
blockquote::before,
q::after,
q::before {
  content: "";
  content: none;
}

[hidden] {
  display: none !important;
}

[disabled] {
  cursor: not-allowed;
}

.sr-only {
  position: absolute;
  top: -9999px;
  left: -9999px;
  clip: rect(1px, 1px, 1px, 1px);
}

input,
select,
label {
  vertical-align: middle;
}

[type=text] {
  line-height: normal;
}

input[type=text],
input[type=email],
input[type=tel],
input[type=submit] {
  padding: 0;
  border: none;
  border-radius: 0;
  background: none;
  -webkit-appearance: none;
}

textarea {
  padding: 0;
  border: 0;
  border-radius: 0;
  background: transparent;
  resize: none;
  resize: vertical;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  appearance: none;
}

address {
  font-style: normal;
}

