/* home-staff
----------------------------------------- */
@use "../../global" as *;

.p-home-staff {
    &__slider {
        padding-bottom: rem(128);
        margin-top: rem(50);
        transform: translateX(-60px);

        @include responsive(lg2) {
            padding-bottom: rem(150);
            margin-top: rem(52);
        }
    }

    &__item {
        width: rem(246) !important;
        height: rem(311) !important;
        position: relative;

        @include responsive(lg2) {
            width: rem(300) !important;
            height: rem(379) !important;
        }

        &:nth-child(odd) {
            transform: translateY(25px);
        }
    }

    &__textBox {
        position: absolute;
        top: rem(263);
        left: 0;

        @include responsive(lg2) {
            top: rem(336);
        }
    }

    &__comment {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(16);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(16 / 16);

        @include responsive(lg2) {
            font-size: rem(20);
        }

        p {
            display: inline-block;
            background: $color-sub;
            padding: rem(7);
        }

        p:nth-child(2) {
            margin-top: rem(8);

            @include responsive(lg2) {
                margin-top: rem(10);
            }
        }
    }

    &__title {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
        margin-top: rem(10);

        @include responsive(lg2) {
            font-size: rem(16);
        }
    }

    &__name {
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(20 / 20);
        margin-top: rem(14);

        @include responsive(lg2) {
            font-size: rem(24);
            margin-top: rem(16);
        }
    }
}

/*==============================================================
Splide
==============================================================*/
.splide__track {
    overflow: visible !important;
}
