/* breadcrumbs
----------------------------------------- */
@use "../global" as *;

.c-breadcrumbs {
    background: #f0f0f0;
    padding: rem(6) 0;

    @include responsive(md) {
        padding: rem(18) 0;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;

        @include responsive(lg) {
            padding-left: rem(1);
        }
    }

    &__item:not(:last-of-type) {
        display: inline;
    }

    &__item:not(:last-of-type)::after {
        content: ">";
        color: $color-text;
        padding: 0 rem(1.2) 0 0;
    }
    &__link {
        font-family: $base-font-family;
        color: $color-text;
        font-size: rem(11);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(14 / 14);

        @include responsive(md) {
            font-size: rem(14);
        }
    }
}

