/* l-inner
----------------------------------------- */
@use "../global" as *;

.l-inner {
    width: 100%;
    max-width: 1024px;
    margin: 0 auto;
    padding: 0 rem(17);

    @include responsive(sm) {
        padding: 0 rem(47);
    }

    @include responsive(lg2) {
        padding: 0 rem(17);
    }

    // @include responsive("md") {
    //     padding-left: 0;
    //     padding-right: 0;
    // }
}

.p-home-subCatch {
    .l-inner {
        padding: rem(106) 0 0 0;

        @include responsive(lg2) {
            padding: rem(138) 0 0 0;
        }
    }
}

.p-home-staff {
    .l-inner {
        padding-top: rem(90);
        padding-bottom: rem(90);

        @include responsive(lg2) {
            padding: rem(156) rem(40) 0 rem(40);
        }
    }
}

.p-home-benefit {
    .l-inner {
        padding-top: rem(5);
        padding-bottom: rem(190);

        @include responsive(lg2) {
            padding-top: rem(137);
        }
    }
}

.p-home-blog {
    .l-inner {
        padding-top: rem(72);
        padding-bottom: rem(10);

        // @include responsive(md) {
        //     padding: rem(88) rem(34);
        // }

        @include responsive(lg2) {
            padding: rem(88) rem(34);
        }
    }
}

.p-home-recruit {
    .l-inner {
        padding-top: rem(100);
        padding-bottom: rem(88);

        @include responsive(lg2) {
            padding: 0 0 rem(138) 0;
        }
    }
}
