/* home-mv
----------------------------------------- */
@use "../../global" as *;

.p-home-mv {
  position: relative;

  @include responsive(lg2) {
    height: rem(823);
  }

  &__catch {
    color: #fff;
    font-family: $second-font-family;
    @include font(48, 60, 400, 0);
    text-shadow: 0px 4px 8px #000;
    z-index: 2;
    position: absolute;
    top: 62%;
    left: 3%;

    @include responsive(lg2) {
      font-size: rem(96);
      top: 34.1%;
      left: 4.1%;
    }
  }

  &__subCatch {
    position: absolute;
    top: 83%;
    left: 0;
    background-color: $color-text;
    color: #fff;
    clip-path: polygon(0 0, 100% 0, 94% 100%, 0 100%);
    font-family: $base-font-family;
    @include font(12, 30, 700, 0);
    // width: 60.8%;
    width: rem(228);
    max-width: 515px;
    padding-left: rem(12);
    z-index: 2;

    @include responsive(lg2) {
      font-size: rem(24);
      font-weight: 700;
      width: 35.6%;
      padding: 0 0 0 rem(58);
      top: 73%;
    }
  }

  &__blog {
    width: 100%;
    position: absolute;
    top: 91%;
    z-index: 2;

    @include responsive(lg2) {
      width: 98.6%;
      top: 87.8%;
    }
  }

  &__blogText {
    background: #fff;
    border: 1px solid $color-text;
    border-radius: 50px;
    font-family: $base-font-family;
    font-size: rem(12);
    font-weight: 400;
    letter-spacing: 0em;
    width: 93.6%;
    padding: rem(10) rem(10) rem(10) 16%;
    margin: 0 auto;
    position: relative;

    @include responsive(md) {
      display: flex;
      justify-content: space-between;
    }

    @include responsive(lg2) {
      display: flex;
      justify-content: space-between;
      font-size: rem(16);
      width: 90%;
      padding: rem(22) rem(10) rem(22) 19.2%;
    }

    &::before {
      content: "NEWS";
      display: flex;
      align-items: center;
      justify-content: center;
      background: #000;
      border-radius: 50px 0 0 50px;
      color: #fff;
      font-weight: 400;
      font-size: 1rem;
      font-family: Viga;
      width: 15%;
      max-width: 186px;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;

      @include responsive(lg2) {
        font-size: rem(24);
        font-weight: 400;
      }
    }
  }

  &__nextWrapper {
    @include responsive(lg2) {
      width: 13%;
    }
  }

  &__next {
    @include responsive(lg2) {
      display: inline-block !important;
      font-family: $second-font-family;
      font-size: rem(14);
      font-weight: 400;
      letter-spacing: 0em;
      position: absolute;
    }
  }

  &__arrow {
    display: inline-block;
    vertical-align: middle;
    color: #222;
    line-height: 1;
    position: relative;
    width: 7px;
    height: 7px;
    position: absolute;
    top: 50%;
    right: rem(-14);
    transform: translateY(-50%) rotate(45deg);

    &::before,
    &::after {
      content: "";
      position: absolute;
      background: currentColor;
    }

    &::before {
      top: 0;
      left: 0;
      right: 0;
      height: 2px;
    }

    &::after {
      top: 0;
      right: 0;
      bottom: 0;
      width: 2px;
    }
  }
}

/*==============================================================
Splide
==============================================================*/
.p-home-mv {
  .splide {
    position: relative;

    @include responsive(lg2) {
      height: rem(823);
    }
    .splide__track {
      height: 100%;
      .splide__list {
        .splide__slide {
          height: 100%;
          figure {
            height: 100%;
            &::before {
              display: none;
            }
            img {
              position: unset;
              height: 100%;
              min-height: 667px;
              font-family: "object-fit: cover;";
              object-fit: cover;
              object-position: bottom;
            }
          }
        }
      }
    }
  }
}
