/* c-arrow
----------------------------------------- */
@use "../global" as *;

.c-circle {
    display: block;
    border: 1px solid $color-text;
    border-radius: 50%;
    background: $color-text;
    width: rem(43);
    height: rem(43);

    @include responsive(lg2) {
        width: rem(59);
        height: rem(59);
    }

    &.-blog {
        background: #fff;
        border: 1px solid #fff;
        width: rem(59);
        height: rem(59);
    }
}

.c-arrow {
    display: block;
    width: 100%;
    height: 100%;
    position: relative;

    &::before {
        content: "";
        display: block;
        left: 3px;
        width: 16px;
        height: 2px;
        background: #fff;
        position: absolute;
        top: 50%;
        left: 48%;
        transform: translate(-50%, -48%);
    }
    &::after {
        content: "";
        display: block;
        left: 3px;
        width: 12px;
        height: 12px;
        border-top: 2px solid #fff;
        border-right: 2px solid #fff;
        border-radius: 2px;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%) rotate(45deg);
        transform: translate(-50%, -50%) rotate(45deg);
    }
    &.-blog::before {
        background: $color-text;
    }
    &.-blog::after {
        border-top: 2px solid $color-text;
        border-right: 2px solid $color-text;
    }
}
