/* hooverUnderline
----------------------------------------- */
@use "../global" as *;

.c-hoverUnderline {
    position: relative;
    display: inline-block;
}

.c-hoverUnderline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background: black;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
}

@media screen and (min-width: 768px) {
    .c-hoverUnderline:hover::after {
        opacity: 1;
    }
}
