/* contact
----------------------------------------- */
@use "../../global" as *;

.p-contact {
    padding: rem(113) 0 rem(152) 0;

    @include responsive(md) {
        padding: rem(117) 0 rem(192) 0;
    }

    .l-container {
        @include responsive(md) {
            max-width: 960px;
        }

        .l-section__text {
            @include font(15, 28, 400, 100, rem);
        }
    }

    &__lead {
        margin-top: rem(80);

        @include responsive(md) {
            margin-top: rem(112);
        }

        .l-section__text {
            margin-top: rem(35);

            @include responsive(md) {
                margin-top: rem(38);
                @include font(15, 28, 400, 100, rem);
            }
        }
    }

    &__progress {
        display: flex;
        justify-content: space-between;
        margin-top: rem(64);
        @include responsive(md) {
            margin-top: rem(86);
        }
    }

    &__progressItem {
        background: #f0f0f0;
        color: #b4b4b4;
        font-family: $base-font-family;
        @include font(15, 22, 600, 150, rem);
        text-align: center;
        width: 31.83%;
        padding: rem(16) 0;
        @include responsive(md) {
            @include font(24, 28, 600, 150, rem);
        }

        &.-current {
            background: $color-accent;
            color: #fff;
        }
    }

    &__progressItem:not(:last-child) {
        margin-right: rem(16);
    }

    &__button {
        text-align: center;
        width: 100%;
        margin-top: rem(64);

        @include responsive(md) {
            width: 69.375%;
            margin: rem(80) auto 0;
        }
    }

    &__button a {
        display: block;
        background: $color-text;
        border: 1px solid $color-text;
        color: #fff;
        @include font(16, 20, 600, 150, rem);
        padding: rem(25) 0;
        transition: all 0.5s ease;
    }

    &__button a:hover {
        background: #fff;
        color: $color-text;
    }
}
