/* section
----------------------------------------- */
@use "../global" as *;

body.subpage {
    .l-main {
        .l-section__titleWrapper {
            position: absolute;
            top: rem(40);
            left: rem(20);

            @include responsive(md) {
                top: rem(60);
                left: rem(114);
            }

            @include responsive(lg2) {
                top: rem(75);
                left: rem(234);
            }
        }
        .l-section__title {
            font-family: $second-font-family;
            background: #fff;
            color: $color-text;
            width: fit-content;
            font-size: rem(40);
            font-weight: 400;
            letter-spacing: 0em;
            line-height: calc(64 / 64);
            padding: rem(3) rem(7);

            @include responsive(md) {
                font-size: rem(64);
                padding: rem(3) rem(10);
            }
        }

        .l-section__title--secondary {
            font-family: $base-font-family;
            background: #fff;
            color: $color-text;
            font-size: rem(18);
            font-weight: 700;
            letter-spacing: 0em;
            line-height: calc(24 / 24);
            width: fit-content;
            margin-top: rem(24);
            padding: rem(3) rem(4);

            @include responsive(md) {
                font-size: rem(24);
                padding: rem(3) rem(10);
                margin-top: rem(18);
            }
        }

        .l-section__title--tertiary {
            font-family: $base-font-family;
            background: #fff;
            color: $color-sub;
            font-size: rem(14);
            font-weight: 700;
            letter-spacing: 0em;
            line-height: calc(16 / 16);
            width: fit-content;
            margin-top: rem(21);

            @include responsive(md) {
                font-size: rem(16);
                padding: rem(7) rem(10);
                margin-top: rem(18);
            }
        }

        .l-section__subTitle {
            color: $color-text;
            font-family: $base-font-family;
            font-size: rem(20);
            font-weight: 700;
            letter-spacing: 0em;
            line-height: calc(20 / 20);
            text-align: center;

            @include responsive(md) {
                font-size: rem(32);
                line-height: calc(32 / 32);
            }

            &.-business {
                text-align: left;
            }

            &.-company {
                text-align: left;
            }
        }

        .l-intro {
            color: $color-text;
            font-family: $base-font-family;
            font-size: rem(12);
            font-weight: 400;
            letter-spacing: 0em;
            line-height: calc(20 / 12);
            margin-top: rem(20);

            @include responsive(md) {
                font-size: rem(16);
                line-height: calc(24 / 16);
                margin-top: rem(44);
            }
        }
    }
}
