.l-section {
    &__title {
        font-family: $base-font-family;
        font-size: rem(32);
        font-weight: 700;
        letter-spacing: 0em;
        text-align: left;
        position: relative;

        @include responsive(lg2) {
            font-size: rem(52);
        }

        &::before {
            color: #9797971a;
            font-family: Poppins;
            font-size: rem(80);
            font-weight: 400;

            @include responsive(lg2) {
                font-size: rem(136);
            }
        }

        &.-staff::before {
            content: "MEMBER";
            letter-spacing: 0em;
            position: absolute;
            top: -73%;
            left: 0;

            @include responsive(lg2) {
                left: rem(100);
            }
        }

        &.-benefit {
            @include responsive(sm) {
                text-align: center;
            }
        }

        &.-benefit::before {
            content: "BENEFITS";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        &.-blog::before {
            content: "BLOG";
            position: absolute;
            top: rem(-28);
            left: 0;

            @include responsive(lg2) {
                top: rem(-50);
                left: rem(-46);
            }
        }

        &.-recruit {
            @media screen {
                @include responsive(lg2) {
                    text-align: center;
                }
            }

            &::before {
                content: "RECRUITMENT";
                position: absolute;
                top: rem(-30);
                left: 0;

                @include responsive(lg2) {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
        }

        span {
            border-bottom: 1px solid $color-text;
        }
    }

    &__description {
        font-family: $base-font-family;
        font-size: rem(16);
        font-weight: 400;

        letter-spacing: 0em;
        line-height: calc(24 / 16);
        margin-top: rem(40);

        @include responsive(lg2) {
            margin-top: rem(50);
        }

        &.-benefit {
            @include responsive(lg2) {
                margin-top: rem(40);
            }

            @include responsive(md) {
                text-align: center;
            }
        }

        &.-blog {
            @include responsive(lg2) {
                width: 34%;
                margin-top: rem(66);
            }
        }

        &.-recruit {
            @include responsive(lg2) {
                text-align: center;
                margin-top: rem(44);
            }
        }
    }
}
