/* hamburger
----------------------------------------- */
@use "../global" as *;

body.subpage {
  .l-hamburger__toggle {
    top: rem(4);

    @include responsive(xxl) {
      top: rem(34);
      right: rem(45);
    }
  }
  .l-hamburger__toggleWrap {
    span {
      background: $color-text;
    }
  }

  .l-hamburger__text {
    color: $color-text;
  }
}

.l-hamburger__toggle {
  cursor: pointer;
  display: block;
  width: rem(46);
  height: rem(39);
  padding: rem(10);
  transition: all 0.5s ease;
  position: absolute;
  top: rem(18);
  right: rem(4);
  z-index: 101;

  @include responsive(lg2) {
    top: rem(48);
    right: rem(23);
  }

  &.is-active {
    .l-hamburger__toggleWrap {
      span {
        background: $color-text;
        &:nth-of-type(1) {
          transform: translateY(0.9rem) rotate(-45deg);

          @include responsive(lg2) {
            transform: translateY(2rem) rotate(-45deg);
          }
        }
        &:nth-of-type(2) {
          opacity: 0;
        }
        &:nth-of-type(3) {
          transform: translateY(-0.8rem) rotate(45deg);
        }
      }
    }
    .l-hamburger__text {
      opacity: 0;
    }
  }
  &:hover {
    opacity: 1;
  }
}

.l-hamburger__toggleWrap {
  position: relative;
  width: 100%;
  height: 100%;
  transition: all 0.5s ease;
  span {
    position: absolute;
    display: inline-block;
    width: rem(32);
    height: rem(2);
    background-color: #fff;
    transition: all 0.5s ease;

    @include responsive(md) {
      width: rem(33);
    }

    @include responsive(lg2) {
      width: rem(51);
    }

    &:nth-of-type(1) {
      top: 0;
      left: 0;

      @include responsive(lg2) {
        top: rem(-13);
      }
    }
    &:nth-of-type(2) {
      top: rem(9);
      left: 0;
      transition: all 0.5s ease;

      @include responsive(lg2) {
        top: rem(2);
      }
    }
    &:nth-of-type(3) {
      bottom: 0;
      left: 0;
    }
  }
}

.l-hamburger__text {
  color: #fff;
  @include font(10, 10, 400, 100, rem);
  font-family: $second-font-family;
  margin-top: rem(6);
  transition: all 0.5s ease;

  @include responsive(lg2) {
    @include font(12, 10, 400, 100, rem);
    letter-spacing: 0em;
    padding-left: rem(8);
    margin-top: rem(10);
  }
}
