/* u-text
----------------------------------------- */
@use "../global" as *;

$alignList: Left, Center, Right, Justify;

@each $align in $alignList {
    .u-text#{$align} {
        text-align: #{$align} !important;
    }
}

@media screen and (max-width: 767px) {
    @each $align in $alignList {
        .u-textMd#{$align} {
            text-align: #{$align} !important;
        }
    }
}
