/* column
----------------------------------------- */
@use "../global" as *;

.c-column2 {
  display: grid;
  gap: 10 10;
  grid-template-columns: repeat(2, 1fr);

  @media screen and (max-width: 767px) {
    grid-template-columns: repeat(1, 1fr);
    gap: 10;
  }
}

.c-column2--gap60 {
  gap: 10;

  @media screen and (max-width: 767px) {
    gap: 10;
  }
}

.c-column2--gapSp20 {
  @media screen and (max-width: 767px) {
    gap: 10;
  }
}

.c-column3 {
  display: grid;
  gap: 10;
  grid-template-columns: repeat(3, 1fr);

  @media screen and (max-width: 767px) {
    grid-template-columns: repeat(1, 1fr);
  }
}

.c-column4 {
  display: grid;
  gap: 10;
  grid-template-columns: repeat(4, 1fr);

  @media screen and (max-width: 767px) {
    grid-template-columns: repeat(1, 1fr);
  }
}

.c-column4--sp2 {
  @media screen and (max-width: 767px) {
    grid-template-columns: repeat(2, 1fr);
  }
}
